#browseHeading {
  text-align: center;
  font-size: 2rem;
  margin-bottom: 10px;
  color: #333;
}
#p1 {
  text-align: center;
  color: #666;
  margin-bottom: 30px;
}
#proudctimgs {
  display: flex;
  justify-content: center;   /* center all items */
  gap: 30px;                 /* space between boxes */
  flex-wrap: nowrap;         /* keep them in one row */
}
.category-item {
  background: #f9f9f9;       /* light box background */
  border-radius: 12px;
  overflow: hidden;          /* ensures image doesn’t overflow box */
  text-align: center;
  box-shadow: 0px 2px 8px rgba(0,0,0,0.1); /* subtle box shadow */
  transition: transform 0.2s ease;
}
.category-item:hover {
  transform: translateY(-5px); /* small lift effect on hover */
}
.category-item img {
  width: 400px;             /* fixed image width */
  height: 400px;            /* fixed image height */
  object-fit: cover;        /* keep aspect ratio without stretching */
  display: block;
}
.category-item h2 {
  margin: 15px 0;
  font-size: 1.2rem;
  font-weight: 500;
  color: #333;
}
