.testimonials {
  text-align: center;
  padding: 30px 20px;
  background: #fff;
}
.testimonials-title {
  font-size: 2rem;
  font-weight: bold;
  margin-bottom: 40px;
  color: #f7c500;
  text-decoration:underline solid cornflowerblue;
}
.testimonial-container {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
  gap: 25px;
  max-width: 1200px;
  margin: auto;
}
.testimonial-card {
  background: #fff;
  padding: 25px;
  border-radius: 15px;
  box-shadow: 0px 4px 12px rgba(0, 0, 0, 0.08);
  position: relative;
  transition: transform 0.3s ease;
}
.testimonial-card:hover {
  transform: translateY(-8px);
}
