.testimonial-img {
  width: 80px;
  height: 80px;
  border-radius: 50%;
  margin: 15px auto;
  object-fit: cover;
}
.testimonial-text {
  font-size: 0.95rem;
  color: #555;
  margin: 15px 0;
  line-height: 1.5;
}
.stars {
  font-size: 1.2rem;
  color: #f7c500;
  margin: 10px 0;
}
.testimonial-name {
  font-size: 1.1rem;
  font-weight: bold;
  margin-top: 5px;
}
.testimonial-role {
  font-size: 0.9rem;
  color: #777;
}
@media(min-width:320px) and (max-width:767px){
.testimonials {
  text-align: center;
  padding: 30px 20px;
  background: #fff;
  width: 89%;
}
.testimonials-title {
  font-size: 2rem;
  font-weight: bold;
  margin-bottom: 40px;
  color: #f7c500;
  text-decoration:underline solid cornflowerblue;
  text-align: center;
  margin-left: 0%;
  margin-right: 0%;
  width: 155%;
}
.testimonial-container {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
  gap: 25px;
  /* max-width: 1200px; */
  margin-left: 30%;
  width: 100%;
}
}
@media(min-width:1024px) and (max-width:1439px){
   .testimonials {
  text-align: center;
  padding: 30px 20px;
  background: #fff;
  width: 160%;
}
.testimonials-title {
  font-size: 2rem;
  font-weight: bold;
  margin-bottom: 40px;
  color: #f7c500;
  text-decoration:underline solid cornflowerblue;
  text-align: center;
  margin-left: 0%;
  margin-right: 0%;
}
.testimonial-container {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(4, 1fr));
  gap: 25px;
  justify-self: center;
  margin-left: 12%;
}
}