#f{
    background-color: #FAF3EA;
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    padding-top: 2%;
    padding-bottom: 2%;
    margin-bottom: 10px;
    overflow-x: hidden;
}
.logos{
    width: 60px;
    height: 60px;
    margin-top: 40px;
    margin-bottom: 40px;
}
.quality{
   display: flex;
   flex-wrap: wrap;
   flex-direction: row;
}
.d{
    margin-top: 20px;
   
    line-height: 17px;
}
.info{
    margin-left: 10px;
}
.warranty{
   display: flex;
   flex-wrap: wrap;
   flex-direction: row;
}
.shipping{
   display: flex;
   flex-wrap: wrap;
   flex-direction: row;
}
.support{
   display: flex;
   flex-wrap: wrap;
   flex-direction: row;
}
@media(min-width:320px) and (max-width:767px){
    #f{
    width: 145vw;
    background-color: #FAF3EA;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    padding-top: 2%;
    padding-bottom: 2%;
    margin-bottom: 10px;
    overflow-x: hidden;
}
.quality{
   justify-content: center;
   margin-left: 12%;
}
.warranty{
   justify-content: center;
}
.shipping{
  justify-content: center;
  margin-right: 7%;
}
.support{
   justify-content: center;
}
hr{
    width: 145vw;
}
}
@media(min-width:768px) and (max-width:1024px){
    #f{
    background-color: #FAF3EA;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    padding-top: 2%;
    padding-bottom: 2%;
    margin-bottom: 10px;
    overflow-x: hidden;
}
.quality{
   justify-content: center;
   margin-left: 12%;
}
.warranty{
   justify-content: center;
   margin-left: 4%;
}
.shipping{
  justify-content: center;
}
.support{
   justify-content: center;
}
hr{
    width: 98%;
}
}
@media(min-width:1024px) and (max-width:1439px){
   #f{
    background-color: #FAF3EA;
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    justify-content: space-around;
    padding-top: 2%;
    padding-bottom: 2%;
    padding-left: 0px;
    padding-right: 0px;
    margin-bottom: 10px;
    overflow-x: hidden;
    width: 160%;
    align-items: center;
    align-content: center;
}
.quality{
   justify-content: center;
   margin-left: 0%;
}
.warranty{
   justify-content: center;
   margin-left: 0%;
}
.shipping{
  justify-content: center;
}
.support{
   justify-content: center;
}
hr{
    width: 158%;
}
}
