#container{
    display: flex;
    flex-direction: row ;
    gap: 410px;
}
.one{
   margin-left: 50px;
   margin-top: 0px;
}
.h{
    font-size: larger;
    font-weight: 700px;
    margin-top: 0px;
    margin-left: 10px;
    color: goldenrod;
}
.m{
    line-height: 40px;
}
.one p{
    font-style: normal;
    font-weight: normal;
    color: black;
}
.two{
    display: flex;
    flex-direction: column;
}
.links{
    font-size: larger;
    margin-top: 0px;
    color: grey;
    text-align: center;
}
.l{
    text-decoration: none;
    color: black;
    line-height: 40px;
    text-align: center;
}
.three{
   margin-left: 0px;
   margin-top: 0px;
}
.help{
    color: grey;
    margin-top: 0px;
    text-align: center;
}
.three p{
    color: black;
    font-weight: normal;
    line-height: 40px;
    text-align: center;
}
#r{
    color: black;
    font-weight: normal;
    margin-top: 10px;
}
@media(min-width:320px) and (max-width:767px){
    #container{
        width: 145vw;
    display: flex;
    flex-direction: row ;
    gap: 20px;
    overflow-x: hidden;
}
.one{
   margin-left: 0px;
   margin-top: 0px;
}
}
@media(min-width:768px) and (max-width:1024px){
    #container{
    display: flex;
    flex-direction: row ;
    gap: 20px;
    justify-content: space-between;
}
.one{
    margin-left: 0px;
}
.three{
    margin-right: 10px;
}
hr{
    width: 103%;
}
}
@media(min-width:1024px) and (max-width:1439px){
    #container{
    display: flex;
    flex-direction: row ;
    gap: 360px;
    justify-content: space-evenly;
    margin: 0px;
    width: 160%;
}
.one{
    margin: 0px;
}
.three{
    margin: 0px;
}
hr{
    width: 158%;
}
}