.contact{
    background-color: aliceblue;
    margin-bottom: 20px;
}
h1{
    text-align: center;
}
.para{
    text-align: center;
    font-weight: normal;
}
label{
    font-size: small;
}
.textbox{
    display: flex;
    align-items: center;
    height: 50px;
    width: 300px;
    text-align: center;
}
#w{
    width: 20%;
    height: 25%;
    margin-bottom: 20px;
}
form{
    margin-right: 0px;
    margin-top: 10px;
}
.co {
    display: flex;
    flex-direction: row;
    justify-content: center;   /* center horizontally */
    align-items: center;       /* center vertically */
    gap: 30px;                 /* space between image and form */
}

#sub {
    margin-left: 70px;
    color: white;
    border: 2px solid #B88E2F;
    background-color: #B88E2F;
    height: 45px;
    width: 150px;
    font-weight: bolder;
    text-align: center;
    cursor: pointer;
    border-radius: 10px;
}
#sub:hover{
    transition: transform 0.2s ease;
    background-color:#ce9103 ;
    color: white;
    border: 2px solid brown;
}
