.navbar {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    padding: 15px 50px;
}
.nav-left {
    display: flex;
    align-items: center;
}
.logo {
    height: auto;
    width: 200px;
    margin-right: 10px;
    background-color: none;
    opacity: 1000%;
}
/* .heading {
    font-size: xx-large;
    text-decoration: none;
    color: black;
    margin-right: 40px;
} */
.nav-links {
    display: flex;
    gap: 50px;
    /* space between links */
}
.nav-links a {
    color: black;
    text-decoration: none;
    font-size: 18px;
    cursor: pointer;
}
.nav-icons {
    display: flex;
    align-items: center;
    gap: 20px;
}
.useralert {
    width: 20px;
    margin-top: 10px;
    margin-right: 20px;
    cursor: pointer;
}
.search {
    width: 20px;
    margin-top: 10px;
    margin-right: 20px;
    cursor: pointer;
}
.wishlist {
    width: 20px;
    margin-top: 10px;
    margin-right: 20px;
    cursor: pointer;
}
.cart {
    width: 20px;
    margin-top: 10px;
    cursor: pointer;
}
.hero {
    height: 90vh;
    /*1vh=1% so 90vh=90% */
    background-size: cover;
    background-position: center;
    display: flex;
    align-items: center;
    padding-left: 900px;
}
.frontcard {
    position: absolute;
    top: 60%;
    left: 50%;
    transform: translateY(-50%);
    background-color: #FAEBD7;
    background-color: #FAEBD7;
    padding: 40px 50px;
    border-radius: 12px;
    max-width: 500px;
    box-shadow: 0 4px 20px rgba(0, 0, 0, 0.1);
}
.firstchild {
    font-size: 14px;
    font-weight: 600;
    color: #333;
    letter-spacing: 2px;
    margin-bottom: 10px;
}
.boxhead {
    color: #A4712A;
    font-size: 52px;
    font-weight: 700;
    line-height: 1.3;
    margin: 10px 0;
    text-align: start;
}
.second {
    font-size: 16px;
    color: #000;
    line-height: 1.6;
    margin: 15px 0 25px;
}
.Buy-Now {
    background-color: #A4712A;
    color: #fff;
    font-size: 16px;
    font-weight: 600;
    padding: 14px 30px;
    border: none;
    border-radius: 6px;
    cursor: pointer;
    transition: all 0.3s ease;
}
.Buy-Now:hover {
    background-color: #8c5d22;
}
a:hover {
    text-decoration: solid blue underline;
    color: goldenrod;
}
a:active {
    color: #8c5d22;
}
