.aboutus{
    display: flex;
    flex-direction: row;
    gap: 40px;
    margin: 20px;
}
.furnitureimg{
    width: 50%;
    height: 60vh;
}
.matter{
    width: 95%;
    font-size: 23px;
    font-weight: normal;
    color: black;
}
.fur{
    color: brown;
    text-align: center;
}
@media(min-width:320px) and (max-width:767px){
    .aboutus{
        display: flex;
        flex-direction: column;
        gap: 20px;
    }
    .furnitureimg{
    width:135vw;
    height: 70vh;
}
.matter{
    width: 140vw;
    font-size: 23px;
    font-weight: normal;
    color: black;
}
.fur{
    color: brown;
    text-align: center;
    margin-left: 25%;
    margin-right: 0px;
}
}
@media(min-width:768px) and (max-width:1024px){
    .aboutus{
        display: flex;
        flex-direction: column;
        gap: 20px;
    }
    .furnitureimg{
    width:95vw;
    height: 60vh;
}
.matter{
    width: 95vw;
    font-size: 20px;
    font-weight: normal;
    color: black;
}
.fur{
    color: brown;
    text-align: center;
    margin-left: 0%;
    margin-right: 0%;
    width: 100%;
}
}
@media(min-width:1024px) and (max-width:1439px){
#abtus{
    width: 160%;
}
    .aboutus{
        display: flex;
        flex-direction: column;
        margin-left: 0px;
        margin-right: 0px;
    }
    .furnitureimg{
    width:90%;
    height: 125%;
    margin-left: 0px;
    margin-right: 0px;
    align-self: center;
}
.matter{
    width: 90%;
    font-size: 24px;
    font-weight: normal;
    color: black;
}
#about .fur{
    color: brown;
    text-align: center;
    width: 100%;
    margin-left: 0%;
    margin-right: 0%;
}
}